"use client";

import { useEffect, useMemo, useRef, useState } from "react";
import Link from "next/link";
import toast, { Toaster } from "react-hot-toast";

import {
  Users,
  Clock,
  BadgeAlert,
  RefreshCw,
  Calendar,
  Download,
  Search,
} from "lucide-react";

const SAMPLE = [
  { id: 3, empId: "E003", name: "Manav Soni", dept: "Sales", desig: "BDE", firstPunch: "04:31 PM", lastPunch: "-", totalHours: "02:11m", breakHours: "-" },
  { id: 16, empId: "E016", name: "Himanshu", dept: "Sales", desig: "BDM", firstPunch: "04:22 PM", lastPunch: "04:24 PM", totalHours: "00:02m", breakHours: "-" },
  { id: 5, empId: "E005", name: "Sanket", dept: "Sales", desig: "BDE", firstPunch: "03:20 PM", lastPunch: "-", totalHours: "03:20m", breakHours: "-" },
  { id: 20, empId: "E020", name: "Dhanmadeep", dept: "Sales", desig: "BDE", firstPunch: "01:00 PM", lastPunch: "-", totalHours: "05:54m", breakHours: "-" },
  { id: 10, empId: "E010", name: "Zuber", dept: "Sales", desig: "BDE", firstPunch: "10:25 AM", lastPunch: "03:38 PM", totalHours: "04:13m", breakHours: "-" },
];

const KPI_CARDS = [
  { title: "Total Employees", value: 32, icon: Users },
  { title: "Currently Working", value: 6, icon: Users },
  { title: "On Break", value: 1, icon: Clock },
  { title: "Time Off", value: 0, icon: BadgeAlert },
  { title: "Pending Biometrics", value: 24, icon: BadgeAlert },
];

export default function DashboardPage() {
  const [date, setDate] = useState(() => new Date().toISOString().slice(0, 10));
  const [shiftTab, setShiftTab] = useState<"all" | "morning" | "general" | "night" | "default">("all");
  const [query, setQuery] = useState("");
  const [rows, setRows] = useState(SAMPLE);
  const [page, setPage] = useState(1);
  const [loading, setLoading] = useState(false);
  const pageSize = 10;

  const firstLoadRef = useRef(true);

  const fetchData = async () => {
    try {
      setLoading(true);

      await new Promise((r) => setTimeout(r, 450));
      setRows(SAMPLE);

      if (firstLoadRef.current) {
        toast.success("Welcome to the Dashboard ✨");
        firstLoadRef.current = false;
      }

      setPage(1);
    } catch {
      toast.error("Failed to fetch data");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchData();
  }, [date, shiftTab]);

  const filtered = useMemo(() => {
    const q = query.trim().toLowerCase();
    return rows.filter(
      (r) =>
        !q ||
        r.name.toLowerCase().includes(q) ||
        r.empId.toLowerCase().includes(q) ||
        r.dept.toLowerCase().includes(q)
    );
  }, [rows, query]);

  const totalPages = Math.max(1, Math.ceil(filtered.length / pageSize));
  const pageRows = filtered.slice((page - 1) * pageSize, page * pageSize);

  const exportCSV = () => {
    try {
      const headers = ["Emp ID", "Name", "Dept", "Designation", "First Punch", "Last Punch", "Total Working Hours", "Total Break Hours"];
      const body = pageRows.map((r) => [
        r.empId,
        r.name,
        r.dept,
        r.desig,
        r.firstPunch,
        r.lastPunch,
        r.totalHours,
        r.breakHours,
      ]);

      const csv = [headers, ...body]
        .map((row) => row.map((c) => `"${String(c).replace(/"/g, '""')}"`).join(","))
        .join("\n");

      const blob = new Blob([csv], { type: "text/csv;charset=utf-8;" });
      const url = URL.createObjectURL(blob);
      const a = document.createElement("a");

      a.href = url;
      a.download = `attendance_${date}.csv`;
      a.click();

      toast.success("CSV exported");
    } catch {
      toast.error("CSV export failed");
    }
  };

  return (
    <div data-theme="corporate" className="p-6 bg-base-200 min-h-screen">
      <Toaster position="top-right" />

      {/* HEADER */}
      <div className="flex items-center justify-between mb-6">
        <div>
          <h1 className="text-3xl font-bold">Dashboard Overview</h1>
          <p className="text-sm opacity-70">Quick summary of today’s activity</p>
        </div>

        <div className="flex items-center gap-2">
          <label className="flex items-center gap-2 border rounded-lg px-3 py-2 bg-base-100 shadow">
            <Calendar className="w-5 h-5 opacity-70" />
            <input
              type="date"
              value={date}
              onChange={(e) => setDate(e.target.value)}
              className="input input-xs bg-transparent p-0"
            />
          </label>

          <button className="btn btn-sm btn-neutral" onClick={fetchData}>
            <RefreshCw className="w-4 h-4" /> Refresh
          </button>

          <button className="btn btn-sm btn-primary" onClick={exportCSV}>
            <Download className="w-4 h-4" /> Export CSV
          </button>
        </div>
      </div>

      {/* KPI STATS */}
      <div className="stats stats-vertical lg:stats-horizontal shadow bg-base-100 mb-6 rounded-xl">
        {KPI_CARDS.map((k, i) => {
          const Icon = k.icon;
          return (
            <div key={i} className="stat">
              <div className="stat-title flex justify-between">
                {k.title}
                <Icon className="w-5 h-5 opacity-70" />
              </div>
              <div className="stat-value">{k.value}</div>
            </div>
          );
        })}
      </div>

      {/* MAIN CARD */}
      <div className="card bg-base-100 shadow-xl">
        <div className="card-body">
          <div className="flex items-center justify-between">
            <h2 className="card-title">Quick Attendance Summary</h2>

            <label className="input input-bordered w-64 flex items-center gap-2">
              <Search className="w-4 h-4 opacity-70" />
              <input
                type="text"
                placeholder="Search name / emp id"
                value={query}
                onChange={(e) => {
                  setQuery(e.target.value);
                  setPage(1);
                }}
              />
            </label>
          </div>

          {/* Tabs */}
          <div role="tablist" className="tabs tabs-bordered mt-4">
            {["all", "morning", "general", "night", "default"].map((tab) => (
              <button
                key={tab}
                role="tab"
                className={`tab ${shiftTab === tab ? "tab-active" : ""}`}
                onClick={() => setShiftTab(tab as any)}
              >
                {tab.toUpperCase()}
              </button>
            ))}
          </div>

          {/* Mini stats */}
          <div className="grid grid-cols-3 gap-4 mt-4">
            <div className="stat bg-base-200 rounded-lg p-3">
              <div className="stat-title text-xs">Checked In</div>
              <div className="stat-value text-lg">7</div>
            </div>
            <div className="stat bg-base-200 rounded-lg p-3">
              <div className="stat-title text-xs">Not In Yet</div>
              <div className="stat-value text-lg">25</div>
            </div>
            <div className="stat bg-base-200 rounded-lg p-3">
              <div className="stat-title text-xs">Time Off</div>
              <div className="stat-value text-lg">0</div>
            </div>
          </div>

          {/* TABLE */}
          <div className="overflow-x-auto mt-4">
            <table className="table table-zebra bg-base-100">
              <thead className="bg-base-300">
                <tr>
                  <th>Emp ID</th>
                  <th>Name</th>
                  <th>Dept</th>
                  <th>Designation</th>
                  <th>First Punch</th>
                  <th>Last Punch</th>
                  <th>Total Working Hours</th>
                  <th>Total Break Hours</th>
                </tr>
              </thead>

              <tbody>
                {loading ? (
                  <tr><td colSpan={8} className="text-center py-6">Loading…</td></tr>
                ) : pageRows.length === 0 ? (
                  <tr><td colSpan={8} className="text-center py-6">No records found.</td></tr>
                ) : (
                  pageRows.map((r) => (
                    <tr key={r.id}>
                      <td>{r.empId}</td>
                      <td>
                        <Link className="link" href={`/employees/${r.id}`}>
                          {r.name}
                        </Link>
                      </td>
                      <td>{r.dept}</td>
                      <td>{r.desig}</td>
                      <td>{r.firstPunch}</td>
                      <td>{r.lastPunch}</td>
                      <td>{r.totalHours}</td>
                      <td>{r.breakHours}</td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>

          {/* Pagination */}
          <div className="flex justify-between items-center mt-4">
            <div className="text-sm">
              Showing {(page - 1) * pageSize + 1}–
              {Math.min(page * pageSize, filtered.length)} of {filtered.length}
            </div>

            <div className="btn-group">
              <button
                className="btn btn-sm"
                disabled={page === 1}
                onClick={() => setPage((p) => Math.max(1, p - 1))}
              >
                Prev
              </button>

              <button className="btn btn-sm btn-primary">
                {page} / {totalPages}
              </button>

              <button
                className="btn btn-sm"
                disabled={page === totalPages}
                onClick={() => setPage((p) => p + 1)}
              >
                Next
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
